// 引入通用弹窗组件
import deviceDialog from '@/components/common/DeviceDialog/deviceDialog.vue';
// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
// 日期格式化组件
import XEUtils from 'xe-utils'; // 注： 在别的组件里调用xe-utils要用this.$utils
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  EQUIPMENT_MONITORING
} from '@/utils/constantsFile';
// 导入所需接口
import { GetDeviceInfo } from '@/api/equipmentmonitoring';
// import { exportDevicesEfficiencyStatistics } from '@/api/export';
export default {
  components: { deviceDialog, VXETable, XEUtils },
  props: {},
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      maxHeight: document.body.clientHeight - 200,
      EQUIPMENT_MONITORING: EQUIPMENT_MONITORING,
      dateTime: '',
      deviceData: [],
      deviceDataHis: [],
      dialogVisible: false,
      hackReset: false,
      pickerOptions: {
        shortcuts: [
          {
            text: BTN_TEXT.LAST_WEEK,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_MONTH,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_THREE_MONTHS,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }
        ]
      },
      tableLoadings: false,
      // 打印数据
      outData: [],
      outColumn: [],
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumn: [],
      tableColumns: [
        {
          field: 'deviceName',
          title: EQUIPMENT_MONITORING.CONTRAST_VALUE
        },
        {
          field: 'mode',
          title: '设备型号',
          width: '',
          align: 'center'
        },
        {
          field: 'devicePriCode',
          title: EQUIPMENT_MONITORING.DEVICEPRICODE,
          width: '',
          align: 'center'
        },
        {
          field: 'commissioningDate',
          title: EQUIPMENT_MONITORING.INPUT_TIME,
          width: '',
          align: 'center'
        },
        // {
        //   field: 'oee',
        //   title: 'OEE',
        //   width: '',
        //   align: 'center'
        // },
        // {
        //   field: 'timeActivation',
        //   title: '时间稼动率',
        //   width: '',
        //   align: 'center'
        // },
        // {
        //   field: 'performanceActivation',
        //   title: '性能稼动率',
        //   width: '',
        //   align: 'center'
        // },
        // {
        //   field: 'yield',
        //   title: '良品率',
        //   width: '',
        //   align: 'center'
        // },
        {
          field: 'runEfficiency',
          title: '平均单日运行效率',
          width: '',
          align: 'center'
        },
        {
          field: 'repairTaskCount',
          title: '故障单数量',
          width: '',
          align: 'center'
        },
        {
          field: 'runTimeHour',
          title: '总运行时长',
          width: '',
          align: 'center'
        },
        {
          field: 'freeTimeHour',
          title: '总空闲时长',
          width: '',
          align: 'center'
        },
        {
          field: 'offlineTimeHour',
          title: '总离线时长',
          width: '',
          align: 'center'
        },
        {
          field: 'warnTimeHour',
          title: '总告警时长',
          width: '',
          align: 'center'
        }
        // {
        //   field: 'averageDailyCapacity',
        //   title: '平均单日产能',
        //   width: '',
        //   align: 'center'
        // },
        // {
        //   field: 'allCapacity',
        //   title: '总产能',
        //   width: '',
        //   align: 'center'
        // }
      ],

      // table分页配置
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      }
    };
  },
  created() {
    this.tableDatas.push({
      addFlag: '1',
      deviceName: '新增对比'
    });
    this.tableColumn = JSON.parse(JSON.stringify(this.tableColumns));
    this.reverseTable();
    const end = new Date();
    const start = new Date();
    start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
    this.dateTime = [
      this.$utils.toDateString(start, 'yyyy-MM-dd') + ' 00:00:00',
      this.$utils.toDateString(end, 'yyyy-MM-dd') + ' 23:59:59'
    ];
  },
  methods: {
    // 反转函数
    reverseTable() {
      const tableDatas = this.tableDatas;
      this.tableData = this.tableColumns.map(column => {
        const item = { 0: column.title };
        tableDatas.forEach((row, rowIndex) => {
          item[rowIndex + 1] = row[column.field];
        });
        return item;
      });
      this.tableColumn = [
        {
          field: '0',
          fixed: 'left',
          width: 200,
          align: 'center'

        }
      ].concat(
        tableDatas.map((item, index) => {
          return {
            field: `${index + 1}`,
            width: 200,
            align: 'center',
            title: item.deviceName,
            slots: {
              default: ({ row, columnIndex, column }) => {
                return [
                  <div>
                    <div v-show={item.addFlag !== '1'}>{row[columnIndex] === 'NaN' ? '无法计算' : this.formatVal(row[columnIndex], row, columnIndex, column, item, index)}</div>
                    <div
                      v-show={
                        item.addFlag === '1' && row[columnIndex] === '新增对比'
                      }
                    >
                      <el-button type="text" onClick={() => this.showDialog()}>
                        {EQUIPMENT_MONITORING.NEW_COMPARISON}
                      </el-button>
                    </div>
                  </div>
                ];
              }
            }
          };
        })
      );
    },
    formatVal(val, a, b, c, d, e) {
      console.log(a, b, c, d, e);
      var arr = ['OEE', '时间稼动率', '性能稼动率', '良品率', '平均单日运行效率'];
      console.log(a[0]);
      if (arr.indexOf(a[0]) >= 0) {
        return Number((val * 100).toFixed(2)) + '%';
      } else {
        return val;
      }
    },
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList() {
      var deviceCodeList = '';
      for (var i in this.deviceData) {
        if (i === '0') {
          deviceCodeList = this.deviceData[0].deviceCode;
        } else {
          deviceCodeList += ',' + this.deviceData[i].deviceCode;
        }
      }
      try {
        this.tableLoadings = true;
        const res = await GetDeviceInfo({
          deviceCodeList: deviceCodeList,
          startTime: this.dateTime[0],
          endTime: this.dateTime[1]
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          for (const i in res.rows) {
            res.rows[i].commissioningDate = this.$utils.toDateString(
              res.rows[i].commissioningDate,
              'yyyy-MM-dd HH:mm:ss'
            );
          }
          this.outData = JSON.parse(JSON.stringify(res.rows));
          // 将请求数据写入table
          this.tableDatas = res.rows;
          this.tableDatas.push({
            addFlag: '1',
            deviceName: '新增对比'
          });
          // 加载动画消失
          this.tableLoadings = false;
          this.reverseTable();
        } else {
          // 提示异常信息
          this.deviceData = this.deviceDataHis;
          showWarningMessage(res.message);
        }
      } catch (error) {
        this.deviceData = this.deviceDataHis;
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },

    /**
     * @description: 设备选择页面数据获取
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    saveRelatedDevice(checkedData) {
      for (var i in checkedData) {
        for (var a in this.deviceData) {
          if (checkedData[i].deviceCode === this.deviceData[a].deviceCode) {
            showWarningMessage('请勿选择重复设备');
            return false;
          }
        }
      }
      this.deviceDataHis = JSON.parse(JSON.stringify(this.deviceData));
      for (const a in checkedData) {
        this.deviceData.push(checkedData[a]);
      }

      // this.deviceData = checkedData;
      this.findList();
      this.handleClose();
    },
    /**
     * @description:关闭弹窗
     * @param {type}
     * @return:
     * @author: CH
     */
    handleClose() {
      this.dialogVisible = false;
      this.hackReset = false;
    },
    /**
     * @description:打开弹窗
     * @param {type}
     * @return:
     * @author: CH
     */
    showDialog() {
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.hackReset = true; // 重建组件
      });
    },
    /**
     * @description:
     * @param {type}
     * @return:
     * @author: CH
     */
    clean() {
      this.tableDatas = [];
      this.tableData = [];
      this.tableColumn = [];
      this.deviceData = [];
      this.deviceDataHis = [];
      this.tableDatas.push({
        addFlag: '1',
        deviceName: '新增对比'
      });
      this.tableColumn = JSON.parse(JSON.stringify(this.tableColumns));
      this.reverseTable();
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTable() {
      var deviceCodeList = '';
      for (var i in this.deviceData) {
        if (i === '0') {
          deviceCodeList = this.deviceData[0].deviceCode;
        } else {
          deviceCodeList += ',' + this.deviceData[i].deviceCode;
        }
      }
      try {
        this.tableLoadings = true;
        const res = await GetDeviceInfo({
          deviceCodeList: deviceCodeList,
          startTime: this.dateTime[0],
          endTime: this.dateTime[1]
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          const option = {
            filename: '多设备数据对比',
            type: 'csv',
            isHeader: false,
            isFooter: false
          };
          this.$refs.xGrid.exportData(option);
          // 加载动画消失
          this.tableLoadings = false;
        } else {
          // 提示异常信息
          this.deviceData = this.deviceDataHis;
          showWarningMessage(res.message);
        }
      } catch (error) {
        this.deviceData = this.deviceDataHis;
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  }
};
